.App {
  text-align: center;
  background-color: black;
  color: white
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}



.Cell {
  width: 120px;
  border: 1px solid black;
  padding: -1px;
  margin: -1 px;
  text-align: left;
  font-weight: 400;
}

.Form{
  font-weight: 500;
}

.Large {
  width: 220px;
}

.Header{
  font-weight: bold;
}


a:visited {
  color: white;
  font-weight: bold;
}
